.item {
  display: -ms-flexbox;
  display: flex;

  position: relative;
  border: solid 5px;
  -webkit-border-image: -webkit-gradient(linear, left top, left bottom, from(#ffb600), to(#000000)) 1;
  -webkit-border-image: -webkit-linear-gradient(top, #ffb600 0%, #000000 100%) 1;
       -o-border-image: -o-linear-gradient(top, #ffb600 0%, #000000 100%) 1;
          border-image: -webkit-gradient(linear, left top, left bottom, from(#ffb600), to(#000000)) 1;
          border-image: linear-gradient(to bottom, #ffb600 0%, #000000 100%) 1;
  cursor: pointer;
  height: 100%;
  width: 100%;
}

.item > img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.caption {
  opacity: 0;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 20%;
  color: white;
  background: #9f7100;
  z-index: 3;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  padding: 10px;
  text-align: center;
}

.item:hover .caption {
  opacity: 1;
}



