body {
  overflow-y: scroll;
}

* {
  margin: 0px;
  padding: 0px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  list-style-type: none;
}

:root {
  --side-padding: 10%;
}

#root {
  padding: 20px var(--side-padding);
}

.itemList {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
}



