.navBar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  height: 100px;
  margin-bottom: 40px;
}

.navBar img {
  height: 100px;
  width: auto;
}

.navBar button {
  padding: 10px 20px;
  font-size: 110%;
  background: white;
  border: 2px solid #383838;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  border-radius: 3px;
  cursor: pointer;
  outline: 0;
}

.navBar button:hover {
  border: 2px solid #f3b312;
  background: #383838;
  color: white;
}



